.navBurger {
   visibility: hidden;
   position: fixed;
   display: flex;
   justify-content: flex-end;
   top: 0;
   right: 0;
   height: 100%;
   width: 100%;
   opacity: 0;
   transition: opacity .4s linear, visibility .4s linear;
}

.navBurger_opened {
   position: fixed;
   background-color: #FBFAF8;
   z-index: 2;
   visibility: visible;
   opacity: 1;
}

.navBurger__container {
   margin: 28px 15px 0;
   padding: 0;
   width: 100%;
   display: flex;
   flex-direction: column;
}

.navBurger__logo {
   padding: 0;
   margin: 0 0 40px -5px;
   width: 145px;
   height: 24px;
}

.navBurger__redirect-button {
   color: #000000;
   background-color: transparent;
   border: 0;
   margin: 0 0 15px;
   padding: 0;
   text-align: start;
   font-size: 18px;
   line-height: 25.2px;
   cursor: pointer;
}